.profile-photo {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
.profile-photo__img {
  height: 200px;
  width: 200px;
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: 100%;
  border-radius: 50%;
  box-sizing: border-box;
  display: inline-block;
  background-color: #6c9fff;
  background-image: url('../images/photo/Small_logo.svg');
  margin-bottom: 16px;
}
.profile-photo__delete-btn {
  position: relative;
  color: #2f0dff;
  margin-left: 13px;
  margin-bottom: 16px;
}
.profile-photo__delete-btn:hover {
  text-decoration: none;
}
.profile-photo__delete-btn::after {
  content: 'x';
  display: block;
  position: absolute;
  bottom: 2px;
  left: -13px;
  color: red;
}
.profile-photo__load-btn {
  width: 180px;
  margin-top: 4px;
  margin-bottom: 16px;
}
.profile-photo__recom {
  opacity: 0.65;
}
.profile-data input {
  max-width: 460px;
}
.birthday {
  display: flex;
  align-items: center;
}
.birthday__day {
  width: 85px;
  margin-right: 10px;
}
.birthday__month {
  width: 85px;
  margin-right: 10px;
}
.birthday__yer {
  width: 100px;
}
.send-code:hover {
  text-decoration: none;
}
.language {
  width: 95px;
}
@media (max-width: 1199px) {
  .user-photo-block {
    order: -1;
  }
}
.section-names {
  color: #000;
  opacity: 0.65;
  text-transform: uppercase;
  margin: 14px 0;
}
.custom-checkbox {
  display: flex;
  flex-direction: column;
  font-size: 24px;
}
.custom-checkbox-small {
  display: flex;
  flex-direction: column;
  font-size: 18px;
  padding-left: 25px;
  position: relative;
}
.custom-checkbox-small.disabled {
  opacity: 0.65;
}
.custom-checkbox-small.disabled::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  margin: 0;
  right: 0;
  background: #000000b4;
  z-index: 21;
}
input[type='checkbox'] {
  height: 0;
  width: 0;
}
input[type='checkbox'] + label {
  position: relative;
  display: flex;
  margin: 0.6em 0;
  align-items: center;
  color: #000;
  transition: color 250ms cubic-bezier(0.4, 0, 0.23, 1);
}
input[type='checkbox'] + label > ins {
  position: absolute;
  display: block;
  bottom: 0;
  left: 2em;
  height: 0;
  width: 100%;
  overflow: hidden;
  text-decoration: none;
  transition: height 300ms cubic-bezier(0.4, 0, 0.23, 1);
}
input[type='checkbox'] + label > ins > i {
  position: absolute;
  bottom: 0;
  font-style: normal;
  color: #2F0DFF;
}
input[type='checkbox'] + label > span {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 1em;
  width: 1em;
  height: 1em;
  background: transparent;
  border: 2px solid #9E9E9E;
  border-radius: 2px;
  cursor: pointer;
  transition: all 250ms cubic-bezier(0.4, 0, 0.23, 1);
}
input[type='checkbox'] + label:hover,
input[type='checkbox']:focus + label {
  color: #0000008f;
}
input[type='checkbox'] + label:hover > span,
input[type='checkbox']:focus + label > span {
  background: rgba(255, 255, 255, 0.9);
}
input[type='checkbox']:checked + label > ins {
  height: 100%;
}
input[type='checkbox']:checked + label > span {
  border: 0.5em solid #EB00FF;
  animation: shrink-bounce 200ms cubic-bezier(0.4, 0, 0.23, 1);
}
input[type='checkbox']:checked + label > span:before {
  content: "";
  position: absolute;
  top: 0.8em;
  left: 0.3em;
  border-right: 3px solid transparent;
  border-bottom: 3px solid transparent;
  transform: rotate(45deg);
  transform-origin: 0% 100%;
  animation: checkbox-check 125ms 250ms cubic-bezier(0.4, 0, 0.23, 1) forwards;
}
@keyframes shrink-bounce {
  0% {
    transform: scale(1);
  }
  33% {
    transform: scale(0.85);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes checkbox-check {
  0% {
    width: 0;
    height: 0;
    border-color: #fff;
    transform: translate3d(0, 0, 0) rotate(45deg);
  }
  33% {
    width: 0.2em;
    height: 0;
    transform: translate3d(0, 0, 0) rotate(45deg);
  }
  100% {
    width: 0.2em;
    height: 0.5em;
    border-color: #fff;
    transform: translate3d(0, -0.5em, 0) rotate(45deg);
  }
}
.reset {
  flex-direction: column;
  align-items: flex-start;
}
.reset__code {
  max-width: 300px;
}
@media (max-width: 362px) {
  .checkbox-text {
    font-size: 20px;
  }
  .custom-checkbox__lebel {
    font-size: 20px;
  }
}
